@import url(https://cdn.rawgit.com/innks/NanumSquareRound/master/nanumsquareround.min.css);
/* FONT */

/* CSS RESET */
*,
::after,
::before {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
    display: block;
}

a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
    padding: 0;
    border: 0;
    margin: 0;
    vertical-align: baseline;
    outline: none;
}

a {
    text-decoration: none;
    outline: none;
    color: inherit;
}

ul,
li {
    list-style: none;
}

img {
    border: 0;
}

button {
    background-color: transparent;
    text-decoration: none;
    border: none;
    outline: none;
    cursor: pointer;
    font: inherit;
}

input {
    text-decoration: none;
    background-color: none;
    outline: none;
    transition: all 0.1s linear;
    border: none;
    padding: 0;
    font-family: 'NanumSquareRound', 'Noto Sans', 'Noto Sans CJK KR', sans-serif;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0px 1000px transparent inset;
    -webkit-transition: background-color 5000s ease-in-out 0s;
    transition: background-color 5000s ease-in-out 0s;
}

textarea {
    resize: none;
    border: none;
    outline: none;
}

select {
    background: none;
    outline: none;
    border-radius: 0;
    cursor: pointer;
    -moz-appearance: none;
    -webkit-appearance: none;
}

/* PLACEHOLDER */

input::-webkit-input-placeholder {
    color: #e6b74a;
    font-size: 14px;
    opacity: 0.8;
}

input:-ms-input-placeholder {
    color: #e6b74a;
    font-size: 14px;
    opacity: 0.8;
}

input::placeholder {
    color: #e6b74a;
    font-size: 14px;
    opacity: 0.8;
}

textarea::-webkit-input-placeholder {
    color: #757575;
    font-size: 14px;
}

textarea:-ms-input-placeholder {
    color: #757575;
    font-size: 14px;
}

textarea::placeholder {
    color: #757575;
    font-size: 14px;
}

/* COMMON */
.clearfix::after {
    content: '';
    display: block;
    clear: both;
}

::-webkit-scrollbar {
    display: none;
}

html,
#root {
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
    font-family: 'NanumSquareRound', 'Noto Sans', 'Noto Sans CJK KR', sans-serif;
    font-size: 17px;
}

